}

/* AddNewProject */
.AddNewProject .add-button span {
  margin-right: 5px;
}
/* ProjectForm */
.ProjectForm {
  background-color: white;
  padding: 1rem;
  border-radius: 4px;
  position: relative;
  padding-bottom: 40px;
}
.ProjectForm input {
  width: 100%;
  height: 40px;
  margin: 15px 0;
  border: none;
  font-size: 1.2rem;
}
.ProjectForm input:focus {
  outline: none;
  border-bottom: 1px solid #0080ff;
}
.ProjectForm button {
  position: absolute;
  bottom: 0;
  height: 40px;
  width: 50%;
  border: none;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: -webkit-filter 300ms ease-in-out;
  transition: filter 300ms ease-in-out;
  transition: filter 300ms ease-in-out, -webkit-filter 300ms ease-in-out;
}
.ProjectForm button.cancel {
  left: 0;
  background-color: #bebebe;
}
.ProjectForm button.confirm {
  right: 0;
  background-color: #0080ff;
}
.ProjectForm button:hover {
  -webkit-filter: brightness(1.1);
          filter: brightness(1.1);
}

/* Todos */
.Todos {
  width: 450px;
  height: 80%;
  background-color: white;

  position: absolute;
  bottom: 40px;
  left: 40px;

  border-radius: 4px;
  opacity: 0.9;

  padding: 0 0.5rem 0.5rem 0.5rem;
  overflow-y: auto;
}
.Todos .selected-project {
  margin: 1rem 0;
  text-transform: capitalize;
  font-size: 1.2rem;
  font-weight: bold;
  padding-bottom: 2px;
