/* Todo */
.Todo {
  padding: 0.5rem 1rem;
}
.Todo .todo-container {
  display: flex;
  align-items: center;
}
.Todo .todo-container span {
  cursor: pointer;
}
.Todo .todo-container .text {
  position: relative;
  flex: 1 1;
  margin: 0 10px;
